// Hero Section
.hero-section {
    background-image: url('../images/hero-section-bkg.jpg');
    background-size: cover;
    background-position: top left;
    min-height: 80vh;
    display: flex;
    align-items: center;

    h1 {
        font-family: $secondary-font;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 1.5rem;
        color: white;
    
        @media (min-width: 768px) {
            font-size: 2.5rem;
        }
    }

    p {
        font-family: $primary-font;
        font-weight: 500;
        font-size: 0.8em;
        color: white;
    }
}

// About Section
.about-section {
    padding: 130px 0;

    .title {
        color: #1a1a1a;
        font-family: $secondary-font;
        font-weight: bold;
        font-size: 1.3rem;
        padding-top: 20px;
        margin-top: 1rem;
        margin-bottom: 25px;
    
        @media (min-width: 768px) {
            font-size: 1.5rem;
            padding-top: 35px;
            margin-top: 0.5rem;
        }

        @media (min-width:992px) {
            font-size:1.9rem;
        }
    }

    .text {
        margin-bottom: 30px;

        p {
            font-family: $primary-font;
            font-weight: 400;
            font-size: 0.9rem;
            color: #555c6a;
    
            // @media (min-width: 768px) {
            //     font-size: 0.8rem;
            // }
    
            // @media (min-width:992px) {
            //     font-size:0.7rem
            // }
        }
    }

    .image-container {
        position: relative;
        width: 100%;

        .text {
            background: $primary-color;
            position: absolute;
            bottom: -10%;
            right: 15%;
            padding: 25px;
            border-top-left-radius: 2em;
            border-bottom-left-radius: 0.6em;
            border-top-right-radius: 0.6em;
            border-bottom-right-radius: 2em;
            box-shadow: 3px 8px 15px change-color($color: $primary-color, $alpha: 0.5);
            
            p {
                margin-bottom: 0px;
                color: #030303;
                font-family: $primary-font;
                font-weight: 500;
                font-size: 1.3rem;
                text-align: left;
            }
        }
    }
}

// Business Vertical
.business-vertical-section {
    padding: 50px 0;
    background: linear-gradient(to bottom, #f2f5f8 0%, #ffffff 100%);

    @media (min-width: 768px) {
        padding: 100px;
    }

    h2 {
        text-align: center;
        font-family: $secondary-font;
        font-weight: 700;
        font-size: 2rem;
        margin-bottom: 28px;

        @media (min-width: 576px) {
            font-size: 3rem;
        }
    }

    .items {
        background-color: transparent;
        border: 1.5px solid #d9d9d9;
        margin-top: 15px;
        padding: 1.25rem;
        border-radius: 15px;
        transition: background-color 0.5s ease-in;
        cursor: pointer;

        @media (min-width: 768px) {
            padding: 30px;
            margin-top: 15px;
        }

        .card-body {
            padding: 0px;

            .icon {
                width: 50px;
                padding-right: 10px;
            }

            .para1 {
                font-family: $secondary-font;
                font-weight: 600;
                margin-bottom: 0;
            }

            .para2 {
                font-family: $primary-font;
                font-weight: 300;
                font-size: 0.9em;
                color: #6e666c;
                margin-bottom: 0.1rem;

                @media (min-width: 768px) {
                    margin-bottom: 1rem;
                }
            }
        }

        &:hover {
            background-color: #ffd500;
            border-color: #ffd500;

            .card-body {
                .para2 {
                    color: #000;
                }
            }
        }
    }
}

// Our Brand Section
.our-brand-section {
    margin-top: 0px;

    @media (min-width: 768px) {
        margin-top: 40px;
    }

    .logo-item {
        display: none;

        &.active {
            display: flex;
        }
    }

    h2 {
        font-family: $secondary-font;
        font-weight: bold;
        padding-top: 60px;
        padding-left: 10px;

        @media (min-width: 992px) {
            padding-top: 0;
        }

        @media (min-width: 1200px) {
            padding-top: 80px;
        }
    }

    p {
        font-family: $primary-font;
        font-weight: 400;
        color: #949494;
        font-size: 0.9em;
        padding-right: 10px;
        padding-left: 10px;
        margin-left: 1px;
        margin-bottom: 50px;

        @media (min-width: 768px) {
            font-size: 1rem;
        }
    }
}

// Company Section
.company-section {
    background-color: #ffd700;
    width: 100%;

    img {
        padding: 15px 5px;
        cursor: pointer;

        @media (min-width: 768px){
            padding:40px 0;
        }
    }
}

// News Section
.news-section {
    padding: 50px 0;

    @media (min-width: 768px) {
        padding: 100px 0;
    }

    .title {
        font-family: $secondary-font;
        font-weight: bold;
        text-align: center;
        margin-bottom: 25px;
        color: #090241;

        @media (min-width: 576px) {
            font-size: 3rem;
            margin-bottom: 25px;
        }
    }

    .news {
        margin-bottom: 30px;

        @media (min-width: 768px) {
            margin-bottom: 0;
        }

        .thumbnail-container {
            width: 100%;
            overflow: hidden;
            display: flex;
            justify-content: center;
            border-top-left-radius: 5em;
            border-bottom-right-radius: 5em;
            position: relative;
    
            img {
                width: auto;
                height: 100%;
                transition: transform ease-in-out 0.3s;
            }

            &::after {
                content: "";
                position: absolute;
                left: -75%;
                z-index: 2;
                width: 50%;
                height: 100%;
                background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .3) 100%);;
                transform: skewX(-25deg);
                transition: left 0.75s;
            }

            &:hover {
                img {
                    transform: scale(1.2);
                }

                &::after {
                    left: 125%;
                }
            }
        }

        .text-container {
            padding: 0;
            padding-top: 18px;
    
            .news-title {
                font-family: $secondary-font;
                font-weight: 700;
                font-size: 1.2rem;
                color: #181818;
    
                @media (min-width: 768px) {
                    font-size: 1.5rem;
                }
            }
    
            .excerpt {
                border-bottom: 1px solid #dbdbdb;
                padding-bottom: 10px;

                p {
                    font-family: $primary-font;
                    font-weight: 300;
                    font-size: 0.9em;
                    color: #404040;
                    margin-bottom: 0;
                }
            }
    
            .meta_data {
                font-family: $secondary-font;
                font-weight: 500;
                font-size: 0.8em;
                text-transform: uppercase;
                padding: 0.8em;
                color: #7a7a7a;
                border-bottom: 1px solid #dbdbdb;
                margin-bottom: 0;
            }
        }
    }
}

