.circle-container
{
    border-radius: 10em;
    overflow: hidden;
    height: 60px;
    width: 60px;
    position: relative;
    margin: 0 auto;
    
    img {
        width: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

    

